class Tags extends React.Component{
	constructor(){
		super()

		this.state = {
			isOn:false,
			tags:['Urgent', 'Internal','Design','Add Functionality' ]
		}
	}

	addTag(e) {
		e.preventDefault()
	}

	render(){
		return (
			<ul>
				{this.state.tags.map((tag,i) => {
					return (
						<li key={i}>
							<button className={this.state.isTagOn} name={tag} value={tag} onClick={this.addTag}>{tag} </button>
						</li>
					)
				})}
			</ul>
		)
	}


}

export default Tags



